html,
body {
  min-width: 320px;
  font-family: "Fira Sans", "Arial", sans-serif;
  font-size: 15px;
  line-height: 26px;
  overflow-x: hidden;
  color: #484848; }

img {
  width: 100%;
  height: auto; }

* {
  box-sizing: border-box; }

.header {
  background-image: url("../../img/promo-bg.jpg");
  background-position: center -40px;
  background-size: cover;
  background-repeat: no-repeat; }

.header__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 1170px;
  margin: 0 auto;
  padding: 0 15px; }

.header__top-bar {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0; }

.header__phone {
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #484848;
  text-decoration: none;
  transition: color ease .3s; }
  .header__phone:hover, .header__phone:focus {
    color: #FFC341; }

.header__callback {
  font-weight: 500;
  font-size: 15px;
  line-height: 18px;
  color: #484848;
  text-decoration: none;
  text-transform: uppercase;
  border-bottom: 1px dashed #484848;
  transition: color ease .3s, border-bottom ease .3s; }
  .header__callback:hover, .header__callback:focus {
    color: #FFC341;
    border-bottom: 1px dashed #FFC341; }

.header__logo-img {
  width: 137px;
  height: 48px; }

.header__title {
  align-self: center;
  max-width: 700px;
  margin-top: 600px;
  margin-bottom: 80px;
  font-size: 48px;
  line-height: 54px;
  font-weight: bold;
  color: #fFFFFF;
  text-align: center; }

.header__btn {
  position: relative;
  align-self: center;
  width: 54px;
  height: 54px;
  padding: 0;
  margin: 0 0 100px 0;
  text-align: center;
  vertical-align: middle;
  background: none;
  border: none;
  border-radius: 70px;
  cursor: pointer;
  border: 1px solid #fFFFFF; }
  .header__btn:before {
    content: "";
    position: absolute;
    top: -14px;
    left: -14px;
    width: 78px;
    height: 78px;
    border-radius: 80px;
    border: 1px dashed #fFFFFF; }
  .header__btn:after {
    content: "";
    position: absolute;
    top: 65px;
    left: 26px;
    width: 1px;
    height: 55px;
    background-color: #fFFFFF; }

.header__btn-icon {
  width: 17px;
  height: 26px; }

@media (max-width: 1366px) {
  .header {
    background-position: center top; }
  .header__title {
    max-width: 345px;
    margin-top: 458px;
    margin-bottom: 32px;
    font-size: 24px;
    line-height: 29px; } }

@media (max-width: 1200px) {
  .header__wrapper {
    width: 100%;
    margin: 0 auto;
    padding: 0 15px; }
  .header__title {
    max-width: 345px;
    margin-top: 358px;
    margin-bottom: 32px;
    font-size: 24px;
    line-height: 29px; } }

@media (max-width: 768px) {
  .header {
    background-image: url("../../img/promo-mobile-bg.jpg");
    background-position: center top; }
  .header__wrapper {
    width: 100%;
    margin: 0 auto;
    padding: 15px 15px; }
  .header__logo {
    flex-basis: 50%;
    order: 10; }
  .header__logo-img {
    width: 120px;
    height: 42px; }
  .header__phone {
    flex-basis: 50%;
    order: 20;
    text-align: right;
    font-size: 14px;
    line-height: 17px;
    margin-top: -35px;
    margin-left: auto; }
  .header__callback {
    order: 30;
    font-size: 14px;
    line-height: 17px;
    margin-left: auto;
    margin-top: -35px; }
  .header__title {
    max-width: 300px;
    font-size: 20px;
    line-height: 24px; } }

